import type { WatchlistDataTabsTableEntity } from "../WatchlistDataTabsTable.types"

type FinancialEndpoint =
  | "companyIncomeStatement"
  | "companyCashFlowStatement"
  | "companyBalanceStatement"

/**
 * Extracts and constructs the fiscal period string from financial data
 * @param rowData - The table row data containing financial information
 * @param endpoint - The specific financial endpoint to extract data from
 * @returns Formatted fiscal period string (e.g., "Q1 2024", "2024")
 */
export function getFiscalPeriod(
  rowData: WatchlistDataTabsTableEntity,
  endpoint: FinancialEndpoint,
): string {
  const data = rowData.financialData[endpoint]?.data
  const fiscalYear = data?.fiscalYear
  const period = data?.period

  return [period, fiscalYear].filter(Boolean).join(" ")
}

/**
 * Convenience functions for specific endpoints
 */
export const getIncomeStatementFiscalPeriod = (rowData: WatchlistDataTabsTableEntity) =>
  getFiscalPeriod(rowData, "companyIncomeStatement")

export const getCashFlowFiscalPeriod = (rowData: WatchlistDataTabsTableEntity) =>
  getFiscalPeriod(rowData, "companyCashFlowStatement")

export const getBalanceSheetFiscalPeriod = (rowData: WatchlistDataTabsTableEntity) =>
  getFiscalPeriod(rowData, "companyBalanceStatement")
